import { ImageResponse } from 'next/og'

export const runtime = 'edge'

export const alt = 'MBTI - MBTI'
export const size = {
  width: 1200,
  height: 630,
}

export const contentType = 'image/png'

// MBTI Type 
const mbtiInfo = {
  'INTJ': { emoji: '', name: '', color: '#6C5CE7' },
  'INTP': { emoji: '', name: '', color: '#A29BFE' },
  'ENTJ': { emoji: '', name: '', color: '#E17055' },
  'ENTP': { emoji: '', name: '', color: '#00B894' },
  'INFJ': { emoji: '', name: '', color: '#00CEC9' },
  'INFP': { emoji: '', name: '', color: '#FD79A8' },
  'ENFJ': { emoji: '', name: '', color: '#FDCB6E' },
  'ENFP': { emoji: '', name: '', color: '#E84393' },
  'ISTJ': { emoji: '', name: '', color: '#2D3436' },
  'ISFJ': { emoji: '', name: '', color: '#636E72' },
  'ESTJ': { emoji: '', name: '', color: '#74B9FF' },
  'ESFJ': { emoji: '', name: '', color: '#FF7675' },
  'ISTP': { emoji: '', name: '', color: '#00B894' },
  'ISFP': { emoji: '', name: '', color: '#FD79A8' },
  'ESTP': { emoji: '', name: '', color: '#FDCB6E' },
  'ESFP': { emoji: '', name: '', color: '#E84393' }
}

export default async function Image({ params }) {
  const mbtiType = params.type.toUpperCase()
  const typeInfo = mbtiInfo[mbtiType] || { emoji: '', name: '', color: '#667eea' }

  return new ImageResponse(
    (
      <div
        style={{
          background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
          width: '100%',
          height: '100%',
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
          justifyContent: 'center',
          fontFamily: 'Arial, sans-serif',
          position: 'relative',
        }}
      >
        {/* Top Badge */}
        <div
          style={{
            background: 'rgba(255, 255, 255, 0.15)',
            borderRadius: '50px',
            padding: '12px 24px',
            marginBottom: '40px',
            backdropFilter: 'blur(10px)',
            border: '1px solid rgba(255, 255, 255, 0.2)',
            display: 'flex',
            alignItems: 'center',
            fontSize: '18px',
            color: 'white',
            fontWeight: '500',
          }}
        >
          MBTI
        </div>

        {/* MBTI Type with Emoji */}
        <div
          style={{
            display: 'flex',
            alignItems: 'center',
            marginBottom: '30px',
          }}
        >
          <div style={{ fontSize: '80px', marginRight: '20px' }}>
            {typeInfo.emoji}
          </div>
          <div
            style={{
              fontSize: '96px',
              fontWeight: 'bold',
              color: typeInfo.color,
              textShadow: '2px 2px 4px rgba(0,0,0,0.3)',
            }}
          >
            {mbtiType}
          </div>
        </div>

        {/* Type Name */}
        <div
          style={{
            fontSize: '36px',
            fontWeight: '600',
            color: 'rgba(255, 255, 255, 0.95)',
            marginBottom: '30px',
            textAlign: 'center',
          }}
        >
          {typeInfo.name}
        </div>

        {/* Subtitle */}
        <div
          style={{
            fontSize: '24px',
            color: 'rgba(255, 255, 255, 0.8)',
            textAlign: 'center',
            maxWidth: '800px',
            lineHeight: '1.4',
          }}
        >
          MBTI
        </div>
      </div>
    ),
    {
      ...size,
    }
  )
}