import { NextResponse } from 'next/server'
import * as userland from "/mnt/c/Users/kangdeokho/Downloads/seniorMBTI/seniorMBTI/senior-mbti-zh/src/app/result/[type]/opengraph-image.js"

const imageModule = { ...userland }

const handler = imageModule.default
const generateImageMetadata = imageModule.generateImageMetadata


  if (typeof handler !== 'function') {
    throw new Error('Default export is missing in "/mnt/c/Users/kangdeokho/Downloads/seniorMBTI/seniorMBTI/senior-mbti-zh/src/app/result/[type]/opengraph-image.js"')
  }
  

export async function GET(_, ctx) {
  const { __metadata_id__ = [], ...params } = ctx.params || {}
  const targetId = __metadata_id__[0]
  let id = undefined
  const imageMetadata = generateImageMetadata ? await generateImageMetadata({ params }) : null

  if (imageMetadata) {
    id = imageMetadata.find((item) => {
      if (process.env.NODE_ENV !== 'production') {
        if (item?.id == null) {
          throw new Error('id property is required for every item returned from generateImageMetadata')
        }
      }
      return item.id.toString() === targetId
    })?.id
    if (id == null) {
      return new NextResponse('Not Found', {
        status: 404,
      })
    }
  }
  return handler({ params: ctx.params ? params : undefined, id })
}
